body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    display: block;
    box-sizing: border-box;
    text-align: right;
    color: #aaa;
    text-decoration: none;
    font-size: 17px;
    padding: 5px;
    cursor: pointer;
    position: absolute;
    top: 2px;
    right: 2px;
    z-index: 1;
}
.wrapper {
    text-align: center;
    box-shadow: 2px 2px 20px 2px rgba(0,0,0,0.6);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    padding-top: 25px;
}

.wrapper .display-close {
    opacity: 0;
    transition: opacity ease 1s;
}

.wrapper:hover .display-close {
    opacity: 1;
}

.timedisplay {
    margin: 0;
    color: #fff;
    height: 100%;
    width: 100%;
}

button {
    color: #fff;
    border: 0;
    background: rgb(89, 89, 89);
    display: block;
    font-size: 1.5rem;
    margin-top: 5px;
    width: 100%;
    min-height: 35px;
    cursor: pointer;
}

button:hover {
    color: #e08700;
}


video {
    margin: 0 auto;
    background-color: #666;
    object-fit: cover;
    position: relative;
    -webkit-user-select: auto;
       -moz-user-select: auto;
        -ms-user-select: auto;
            user-select: auto;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

.resize-wrapper {
    overflow: hidden;
    width: 100%;
    height: 100%;
    box-shadow: 2px 2px 20px 2px rgba(0,0,0,0.6);
}

.resize-wrapper .display-close {
    opacity: 0;
    transition: opacity ease 1s;
}

.resize-wrapper:hover .display-close {
    opacity: 1;
}
.app {
  position: fixed;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  min-height: 100%;
  max-width: 100%;
  background-color: rgba(10,10,10,1);
  background: linear-gradient(to bottom, rgba(50,50,50,1) 0%, rgba(20,20,20,1) 100%);
  font-family: Arial, Helvetica;
  color: white;
}
